@use "global" as *;

.breadcrumb__nav {
}

.breadcrumb__list {
    display: flex;
    font-family: 'Roboto';
    font-size: rem(16);
    font-weight: $normal;
    line-height: rem(19);
    letter-spacing: rem(1);

    @include mq (md) {
        letter-spacing: rem(2);
    }
}

.breadcrumb__item {
}

.breadcrumb__item:not(:first-child) {
    margin-left: rem(2);
}

.breadcrumb__link {
    display: block;
    height: inherit;

}